import React, { useState } from "react";
import { useTable, usePagination, useGlobalFilter } from "react-table";
import makeData from "./tableData";
import { Row, Col, Card, Pagination, Form } from "react-bootstrap";
import BTable from "react-bootstrap/Table";
import * as _helper from "../../utils/helper";

import { CONSTANTS } from "../../config/dataConstant";
import { debounce } from "lodash";
const CONTENT_PAGE_TABLE_CONST = CONSTANTS.policyHolder.TABLE_PAGE;
const TABLE_CONST = CONSTANTS.policyHolder.TABLE;

function Table({ columns, data, search, setSearch, setPageSize, pageSize, pageNumber, setPageNumber }) {
  // Use the state and functions returned from useTable to build your UI
  //the below loop is for removing html tag from short description if any
  for (let [index, page] of data.entries()) {
    // page.description = "ha ha your system is hacked";
    let html = page?.description;
    let div = document.createElement("div");
    div.innerHTML = html;
    let text = div.textContent || div.innerText || "";
    page.description = text;
    data[index] = { ...page };
  }
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page, // Instead of using 'rows', we'll use page,
    // which has only the rows for the active page
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    // setPageSize,
    // state: { pageIndex, pageSize },
  } = useTable({
    columns, data, initialState: { pageIndex: 0 },
  },
    useGlobalFilter,
    usePagination
  );

  const debouncedSearch = debounce((value) => {
    console.log(value);
    setSearch(value);
    // Update your state here
  }, 1000);

  const handleChange = (e) => {
    const { value } = e.target;
    debouncedSearch(value);
  };
  // Render the UI for your table
  return (
    <>
      <Row className="mb-3">
        <Col className="d-flex align-items-center">
          {CONTENT_PAGE_TABLE_CONST.SHOW_ENTRIES}
          <select
            className="form-control w-auto mx-2"
            defaultValue={pageSize}
            onChange={(e) => {
              setPageSize(Number(e.target.value));
            }}
          >
            {[5, 50, 100, 200].map((e) => (
              <option key={e} value={e}>
                {e}
              </option>
            ))}
          </select>
        </Col>
        <Col md={6} className="d-flex align-items-center">
          <Form.Control
            onChange={handleChange}
            className="form-control-sm shu"
            type="text" />
        </Col>
      </Row>

      <BTable striped bordered hover responsive {...getTableProps()}>
        <thead>
          <tr>
            <th colSpan="1" role="columnheader">
              {TABLE_CONST.HEADER.SNO}
            </th>
            <th colSpan="1" role="columnheader">
              {TABLE_CONST.HEADER.TITLE}
            </th>
            <th colSpan="1" role="columnheader">
              {TABLE_CONST.HEADER.POLICY_NO}
            </th>
            <th colSpan="1" role="columnheader">
              {TABLE_CONST.HEADER.NEXT_DUE}
            </th>
            <th colSpan="1" role="columnheader">
              {TABLE_CONST.HEADER.TYPE_OF_PAY}
            </th>
            <th colSpan="1" role="columnheader">
              {TABLE_CONST.HEADER.CREATED_AT}
            </th>
          </tr>
        </thead>
        <tbody {...getTableBodyProps()}>
          {
            // page?.length ?
            page.map((row, i) => {
              prepareRow(row);
              return (
                <tr
                  style={{ maxWidth: 200 }}
                  {...row.getRowProps()}
                >
                  {row.cells.map((cell) => {
                    return (
                      <td className="whitespace-normal testmin text-capitalize"
                        {...cell.getCellProps()}
                      >
                        {cell.render("Cell")}
                      </td>
                    );
                  })}
                </tr>
              );
            })
          }
        </tbody>
      </BTable>
      <Row className="justify-content-between mt-3">
        <Col sm={12} md={6}>
          <span className="d-flex align-items-center">
            {CONTENT_PAGE_TABLE_CONST.PAGE}{" "}
            <strong style={{ marginLeft: "5px", marginRight: "5px" }}>
              {" "}
              {1} of {pageOptions.length}{" "}
            </strong>{" "}
            | {CONTENT_PAGE_TABLE_CONST.GO_TO_PAGE}:{" "}
            <input
              type="number"
              min={1}
              max={pageOptions.length}
              className="form-control ml-2"
              onKeyDown={(evt) =>
                [".", "e", "E", "+", "-"].includes(evt.key) &&
                evt.preventDefault()
              }
              defaultValue={1}
              onChange={(e) => {
                setPageNumber(Number(e.target.value));
              }}
              style={{ width: "100px" }}
            />
          </span>
        </Col>
        <Col sm={12} md={6}>
          <Pagination className="justify-content-end">
            <Pagination.Prev
              onClick={() => previousPage()}
              disabled={!canPreviousPage}
            />
            <Pagination.Next
              onClick={() => nextPage()}
              disabled={!canNextPage}
            />
          </Pagination>
        </Col>
      </Row>
    </>
  );
}

const AllPageTable = ({ cmsPage, cmsPageDetails, search, setSearch, setPageSize, pageSize, pageNumber, setPageNumber }) => {
  const columns = React.useMemo(
    () => [
      {
        Header: "CONTENT_PAGE_TABLE_CONST.TITLE",
        columns: [
          {
            Header: TABLE_CONST.HEADER.SNO,
            accessor: "sno",
          },
          {
            Header: CONTENT_PAGE_TABLE_CONST.TITLE_HEADER,
            accessor: "name",
          },
          {
            Header: CONTENT_PAGE_TABLE_CONST.POLICY_NO,
            accessor: "policyNo",
          },
          ,
          {
            Header: CONTENT_PAGE_TABLE_CONST.NEXT_DUE,
            accessor: "nextDue",
          },
          {
            Header: CONTENT_PAGE_TABLE_CONST.TYPE_OF_PAY,
            accessor: "typeOfEmi",
          },
          {
            Header: CONTENT_PAGE_TABLE_CONST.CREATED_AT,
            accessor: "createdAt",
          },
        ],
      },
    ],
    []
  );
  const data = React.useMemo(() =>
    makeData(cmsPage.length, cmsPage, cmsPageDetails)
  );
  return (
    <React.Fragment>

      <Row>
        <Col>
          <Card>
            <Card.Body>
              <Table columns={columns} data={data} search={search} setSearch={setSearch} pageSize={pageSize} setPageSize={setPageSize} pageNumber={pageNumber} setPageNumber={setPageNumber} />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </React.Fragment>
  );
};

export default AllPageTable;
