const range = (len) => {
  const arr = [];
  for (let i = 0; i < len; i++) {
    arr.push(i);
  }
  return arr;
};

  /* Function for create data */
const newPerson = (data, sno, cmsPageDetails) => {
  return {
    sno: sno,
    name: (
      <>
        <div className="table__btnouter">
          <button
            className="table__btn"
            onClick={() => cmsPageDetails(data)}
            style={{
              width: "100%",
              background: "transparent",
              textAlign: "left",
              outline: "none",
              border: "none",
              color: "inherit",
            }}
          >
            {data?.name}
          </button>
        </div>
      </>
    ),
    policyNo: data?.policyNo ? data?.policyNo : '--',
    typeOfEmi: data?.typeOfEmi ? data?.typeOfEmi : '--',
    createdAt: data?.createdAt.substr(0, 10),
    nextDue: data?.nextDue.substr(0, 10),
  };
};

export default function makeData(...lens) {
  const makeDataLevel = (depth = 0) => {
    const len = lens[depth];
    return range(len).map((d) => {
      return {
        ...newPerson(lens[depth + 1][d], d + 1, lens[depth + 2]),
        subRows: lens[depth + 1] ? makeDataLevel(depth + 1) : undefined,
      };
    });
  };

  return makeDataLevel();
}
