import api from "../../utils/api";
import * as _helper from "../../utils/helper";
import * as URL from "./index";


export const addPolicyHolder = async (body) => {
  try {
    const response = await api.post(URL.POLICY_HOLDER, body);
    // _helper.sendResponse('success', response.data.message)

    // console.log("response GetUserList** ", response)
    return response;
  } catch (error) {
    // _helper.sendResponse('error', error.message)
    console.log(error, "<<");
    return;
  }
};

export const GetList = async (body) => {
  try {
    let url = URL.POLICY_HOLDER
    if (body?.pageSize) {
      url += '?pageSize=' + body?.pageSize
    }
    if (body?.pageNumber) {
      url += '&pageNumber=' + body?.pageNumber
    }
    if (body?.search) {
      url += '&search=' + body?.search
    }
    const response = await api.get(url);
    // _helper.sendResponse('success', response.data.message)

    // console.log("response GetUserList** ", response)
    return response;
  } catch (error) {
    // _helper.sendResponse('error', error.message)
    console.log(error, "<<");
    return;
  }
};

