export const updatePolicyHolder = async (id, body) => {
  try {
    const response = await api.patch(URL.POLICY_HOLDER + '/' + id, body);
    _helper.sendResponse('success', response.data.message)

    // console.log("response GetUserList** ", response)
    return response;
  } catch (error) {
    // _helper.sendResponse('error', error.message)
    console.log(error, "<<");
    return;
  }
};


