import * as qs from "query-string";
import toast from "react-hot-toast";
import { Badge } from "react-bootstrap";
// import { createDecipheriv } from "crypto";
// import { CONFIG } from "../config/constant";

export const getQueryParams = (param) => {
  const location = window.location.search;
  const parsed = qs.parse(location);
  const response =
    param === "tab"
      ? parseInt(parsed[param] ? parsed[param] : 0)
      : parsed[param]
      ? `${parsed[param]}`
      : "";
  return response;
};

const successMessage = (msg) =>
  toast.success(msg, {
    style: { borderRadius: "10px", background: "#28a745", color: "#fff" },
  });
const errorMessage = (msg) =>
  toast.error(msg, {
    style: { borderRadius: "10px", background: "#dc3545", color: "#fff" },
  });
const infoMessage = (msg) =>
  toast(msg, {
    style: { borderRadius: "10px", background: "#17a2b8", color: "#fff" },
  });
const warningMessage = (msg) =>
  toast(msg, {
    style: { borderRadius: "10px", background: "#ffc107", color: "#fff" },
  });

export const sendResponse = (type, message) => {
  if (type === "success") {
    successMessage(message);
  }
  if (type === "error") {
    errorMessage(message);
  }
  if (type === "warning") {
    warningMessage(message);
  }
  if (type === "info") {
    infoMessage(message);
  }
};

export const emailValidation = (email) => {
  const regexp = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
  return regexp.test(email);
};
export const passwordValidation = (password) => {
  const regexp =
    /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{4,}$/;
  return regexp.test(password);
};
export const contactNumberValidation = (contactNumber) => {
  const regexp = /^[6-9]\d{9}$/;
  // const regexp = /^(?:(?:\+|0{0,2})91(\s*[\-]\s*)?|[0]?)?[6-9]\d{9}$/
  return regexp.test(contactNumber);
};
export const numberValidation = (number) => {
  const regexp = /^\d+$/;
  return regexp.test(number);
};

export const upiValidation = (upi) => {
  const regexp = /^\w+([\.-]?\w+)*@\w+$/;
  return regexp.test(upi);
};

export const successBadge = <Badge variant="success">New</Badge>;
export const warningBadge = <Badge variant="warning">Modified</Badge>;
export const dangerBadge = <Badge variant="danger">Deleted</Badge>;
