import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { Row, Col, Button, Card, Dropdown } from "react-bootstrap";
import * as _ from 'lodash';
import { GetList } from '../../services/api/police-holder';
import Loader from "../../components/loader";
// import { deleteAllPage } from '../../services/api/allPages';
import AllPageTable from "./table";
import * as _helper from '../../utils/helper';

const AllPage = () => {
  const [cmsPageList, setCmsPageList] = useState([]);
  const [search, setSearch] = useState([]);
  const [pageSize, setPageSize] = useState(5)
  const [pageNumber, setPageNumber] = useState(1)
  const [loading, setLoading] = useState(true);
  const history = useHistory();

  /* Function for get all pages */
  useEffect(() => {
    async function fetchData() {
      let users = await GetList();
      if (users?.status == 200) {
        users?.data?.data?.data?.length ? setCmsPageList(users?.data?.data?.data) : setCmsPageList([]);
      }
      setLoading(false);
    }
    fetchData();
  }, [loading]);

  /* Function for get data on search */
  useEffect(() => {
    async function fetchData() {
      let users = await GetList({ search, pageSize, pageNumber });
      if (users?.status == 200) {
        users?.data?.data?.data?.length ? setCmsPageList(users?.data?.data?.data) : setCmsPageList([]);
      }
      setLoading(false);
    }
    fetchData();
  }, [search, pageSize,pageNumber]);

  /* Function for send to selected page */
  const cmsPageDetails = (cmsPage) => {
    if (cmsPage) {
      const route = "/policy-holder/details?edit=true&tab=0&id=" + cmsPage?._id;
      history.push(route);
    }
  }

  return (
    <React.Fragment>
      <Loader loaded={loading} />
      <Row>
        <Col sm={6}>
          <Link to="#" >
            <Button onClick={() =>
              history.push("/policy-holder/details")} className="btn-primary btn-rounded float-right btn-sm">
              {"Add New Policy Holder"}
            </Button>
          </Link>
        </Col>
      </Row>
      <hr />
      <AllPageTable cmsPage={cmsPageList} cmsPageDetails={cmsPageDetails} search={search} setSearch={setSearch} pageSize={pageSize} setPageSize={setPageSize} pageNumber={pageNumber} setPageNumber={setPageNumber} />

    </React.Fragment>
  );
};

export default AllPage;
